import { createGlobalStyle } from 'styled-components';
import { themedPalette } from './themeAtoms';

export const GlobalStyle = createGlobalStyle`

    body {
        background-color: ${({theme}) => theme.bg_page1};
    }
    
    .bg_2 {
        background-color: ${({theme}) => theme.bg_page2};
    }

    .bg_el1 {
        background-color: ${({theme}) => theme.bg_element1};
    }

    .bg_el2 {
        background-color: ${({theme}) => theme.bg_element2};
    }
    
    .bg_el3 {
        background-color: ${({theme}) => theme.bg_element3};
    }

    .bg_el4 {
        background-color: ${({theme}) => theme.bg_element4};
    }
    
    .bg_el5 {
        background-color: ${({theme}) => theme.bg_element5};
    }
    
    .text1 {
        color:${({theme}) => theme.text1};
    }

    .text2 {
        color:${({theme}) => theme.text2};
    }

    .text3 {
        color:${({theme}) => theme.text3};
    }

    .text4 {
        color:${({theme}) => theme.text4};
    }

    .border1 {
        border-color: ${({theme}) => theme.border1};
    }
    
    .border2 {
        border-color: ${({theme}) => theme.border2};
    }

    .border3 {
        border-color: ${({theme}) => theme.border3};
    }

    .border4 {
        border-color: ${({theme}) => theme.border4};
    }
    
    .textReverse {
      color: ${({theme}) => theme.textReverse};
    }

    .themeToggleText {
        color:${({theme}) => theme.text1};
    }
    
    .title1 {
      color:${({theme}) => theme.title1};
    }
    
    .title2 {
      color:${({theme}) => theme.title2};
    }

    .cssVar {
        color:${themedPalette.text};
        background: ${themedPalette.background};
    }

    .bgEl4 {
        background: ${themedPalette.bgEl4};
    }

    .br3 {
        border-color:${themedPalette.border3};
    }

    .defaultPrimary {
        color:${({theme}) => theme.defaultPrimary};
    }

    .defaultAlert {
        color:${({theme}) => theme.defaultAlert};
    }

    .primary1 {
        color:${({theme}) => theme.primary1};
    }

    .alert1 {
        color:${({theme}) => theme.alert1};
    }
    
    .bg_sticker {
      background-color: ${({theme}) => theme.bg_sticker};
    }

    .bg_circle {
        background-color: ${({theme}) => theme.bg_circle};
    }

    .inner_box {
        background-color: ${({theme}) => theme.inner_box};
    }
    
    .link_btn {
      background-color: ${({theme}) => theme.linkBtn};
    }
    
    .link_color {
      color:${({theme}) => theme.linkColor};
    }
    
    .footer {
      background-color: ${({theme}) => theme.footer};
    }
    
    .buttonBack {
      background-color: ${({theme}) => theme.buttonBack};
    }
    
    .buttonColor {
      color:${({theme}) => theme.buttonColor};
    }
    
`;
// themedPalette -> styled-components css variable  --
// themeAtoms.js      
//    css root     
// --> , perfers-color-scheme   ,,,  themeProvider     

// --> document.documentElement.setAttribute('data-theme', theme);  storage   :root  .
// css variable   , theme.js public.scss  2    .
export default GlobalStyle;